import { Component, OnInit, SimpleChanges } from "@angular/core";
import { IStageDropVM } from "app/shared/models/general/general";
import { GeneralService } from "app/shared/services/General/general.service";

@Component({
  selector: "app-home-educational-stages",
  templateUrl: "./home-educational-stages.component.html",
  styleUrls: ["./home-educational-stages.component.scss"],
})
export class HomeEducationalStagesComponent implements OnInit {
  Stages: IStageDropVM[] = [];

  constructor(private generalServ: GeneralService) {}

  ngOnInit(): void {
    this.getEduStagesList();
  }

  getEduStagesList() {
    this.generalServ.getAllEduStagesList<IStageDropVM[]>().subscribe((res) => {
      this.Stages = res;
    });
  }
}
