import { Component, OnInit } from "@angular/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import { OwlOptions } from "ngx-owl-carousel-o";

@Component({
  selector: "app-new-home-component",
  templateUrl: "./new-home-component.component.html",
  styleUrls: ["./new-home-component.component.scss"],
})
export class NewHomeComponentComponent implements OnInit {
  teachers?: any = [];

  constructor(public authservice: AuthService) {}

  ngOnInit(): void {}

  customOptions: OwlOptions = {
    autoplay: true,
    loop: true,
    rewind: true,
    margin: 20,
    dots: false,
    navSpeed: 300,
    navText: [
      '<i class="fas fa-arrow-right"></i>',
      '<i class="fas fa-arrow-left"></i>',
    ],
    responsive: {
      0: {
        items: 2,
      },
      400: {
        items: 2,
      },
      740: {
        items: 4,
      },
      940: {
        items: 6,
      },
    },
    nav: false,
  };

  logout() {
    this.authservice.logout();
  }
}
